/**
 * Common styles
 */
// @include dev-outlines

html
  font-size: 62.5%
  scroll-behavior: smooth

body
  font-size: 1.6rem
  font-family: 'Inter'
  color: $color-font-text
  line-height: 1.5
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  counter-reset: subtitle-counter

@mixin main-container
  width: 90%
  max-width: 1500px
  margin: 0 auto
  @include max-width($screen-md)
    width: 95%

@mixin abs-cover
  position: absolute
  top: 0
  right: 0
  bottom: 0
  left: 0
  z-index: -1

@mixin flex-center-x
  display: flex
  justify-content: space-between
  align-items: center

@mixin flex-center
  display: flex
  justify-content: center
  align-items: center

@mixin decor-line
  position: absolute
  top: 0
  bottom: 0
  width: 1px
  background: $color-line-light

.object-cover
  width: 100%
  height: 100%
  object-fit: cover
  object-position: center

.object-contain
  width: 100%
  height: 100%
  object-fit: contain
  object-position: center

.overflow-x-hidden
  overflow-x: hidden

// Components
.lines
  position: fixed
  top: 0
  right: 0
  left: 0
  height: 100%
  pointer-events: none

  &--light
    opacity: .15

  .col
    position: relative
    height: 100%

    @include max-width($screen-lg)
      display: none

  &::before,
  &::after,
  .col::after
    content: ""
    position: absolute
    top: 0
    bottom: 0
    width: 1px
    background: #eee

  &::before
    left: 0

  &::after,
  .col::after
    right: 0

  &--no-last
    &::after
      display: none

.container
  @include main-container

  & + &
    margin-top: 80px

.section
  padding-top: 160px

  &--pad-btm
    padding-bottom: 160px

  @include max-width($screen-lg)
    padding-top: 80px

    &--pad-btm
      padding-bottom: 80px

  &--full
    position: relative
    z-index: 10
    padding: 80px 0

.mb-lg
  margin-bottom: 160px

  @include max-width($screen-lg)
    margin-bottom: 80px

.gradient
  background: url($noise-url), linear-gradient(90deg, $color-gradient-first 0%, $color-gradient-second 65.63%)

.row
  @include min-width($screen-lg)
    display: flex

.col
  @include min-width($screen-lg)
    width: 25%

  &:nth-child(2)
    flex: 1
    width: 100%

.color-accent
  color: $color-accent

.stars
  display: flex

.star
  width: 16px
  height: 16px
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='15' viewBox='0 0 16 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8 0L9.79611 5.52786H15.6085L10.9062 8.94427L12.7023 14.4721L8 11.0557L3.29772 14.4721L5.09383 8.94427L0.391548 5.52786H6.20389L8 0Z' fill='%230071FE'/%3E%3C/svg%3E%0A")

.title-lg
  font-size: 3.2rem
  font-weight: 400
  line-height: 1.2
  margin-bottom: 1.2em
  max-width: 730px

  // @include max-width($screen-md)
  //   font-size: 2rem

.subtitle
  margin-bottom: 2em
  color: $black
  font-size: 1.6rem
  font-weight: 400
  margin-top: .5em

  // @include max-width($screen-md)
  //   font-size: 1.2rem

  &--white
    color: $white

    &::before
      color: $white !important

  &::before
    counter-increment: subtitle-counter
    content: counter(subtitle-counter,decimal-leading-zero)
    color: $color-accent
    padding-right: .4em

.text-lg
  font-size: 1.6rem
  max-width: 700px

  & + &
    margin-top: 1em

  // @include max-width($screen-md)
  //   font-size: 1.2rem

.img-md
  position: relative
  margin: 80px 0

  @include max-width($screen-lg)
    margin: 40px 0

  &::before
    content: ''
    display: table
    padding-top: 44%

  img
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    object-fit: cover
    object-position: center

// Sections
.header
  position: absolute
  top: 0
  left: 0
  z-index: 20
  width: 100%
  border-bottom: 1px solid $color-line-light
  color: $white
  backdrop-filter: blur(5px)

  // &::before
  //   content: ''
  //   position: absolute
  //   top: 0
  //   right: 0
  //   bottom: 0
  //   left: 0
  //   z-index: -1
  //   background: #000
  //   opacity: .2

  &__inner
    @include flex-center-x
    min-height: $header-mobile-height

  &__logo
    svg
      width: 87px

  &__right
    @include flex-center-x

  &__nav
    @include max-width($screen-lg)
      display: none

    &__list
      display: flex
      font-size: 1.4rem

      * + *
        margin-left: 2.5rem

  &__msgers
    @include flex-center-x
    margin: 0

    @include min-width($screen-sm)
      margin: 0 3rem

    @include min-width($screen-lg)
      margin: 0 6rem

    * + *
      margin-left: 0.4rem

    &__link
      @include flex-center
      width: 36px
      height: 36px
      > svg
        width: 20px
        height: 20px
    &__item
      &--phone
        @include min-width($screen-sm)
          display: none


  &__phone
    text-align: right
    @include max-width($screen-sm)
      display: none

    &__number,
    &__time
      display: block

    &__number
      font-weight: 600

    &__time
      font-size: .75em
      opacity: .7

.btns
  margin-top: 80px

  * + *
    margin-left: 20px

.btn
  display: inline-block
  font-size: 1.3rem
  font-weight: 600
  padding: 1.2em 2em

  &--white
    background: $white
    color: $color-accent

  &--ghost
    color: $white
    background: rgba($white, .1)

  &--primary
    background: $color-accent
    color: $white

  &--secondary
    background: #f0f3fd
    color: lighten($color-accent, 10%)

.menu-btn
  @include flex-center
  flex-direction: column
  width: $header-mobile-height
  height: $header-mobile-height

  @include min-width($screen-lg)
    display: none

  &__bar
    display: block
    width: 30%
    height: 2px
    margin: 3px 0
    background: $white

.hero
  position: relative
  z-index: 15
  width: 100%
  padding-top: $header-mobile-height
  color: $white

  &__inner
    position: relative

    &::before
      content: ''
      @include decor-line

  &__social
    display: flex
    align-items: center

    @include max-width($screen-lg)
      display: none

    &__list
      display: flex
      flex-direction: column

    &__link
      display: flex
      padding: 1rem

      svg
        fill: $white
        width: 2.5rem
        height: 2.5rem

  &__main
    position: relative

    &::before,
    &::after
      content: ''
      @include decor-line

    &::before
      left: 0

    &::after
      right: 0

  &__msg
    width: 100%
    padding: 150px 0

    @include min-width(1300)
      padding: 260px 0

    &--small
      padding: 80px 0 !important

  &__subtitle
    font-size: 1.6rem
    margin-bottom: 1em
    opacity: 0.8

    // @include max-width($screen-md)
    //   font-size: 1rem

  &__title
    font-size: 3.5rem
    max-width: 720px
    margin-bottom: 2em

    // @include max-width($screen-md)
    //   font-size: 2rem

  &__btns
    * + *
      margin-left: 1.5em

  &__stats
    display: flex
    border-top: 1px solid $color-line-light

  &__bg
    @include abs-cover

    &::before
      content: ""
      @include abs-cover
      background-color: darken($color-accent, 45%)
      z-index: 1
      opacity: .7

    &--light
      &::before
        opacity: .5

.stat
  flex: 1
  @include flex-center
  padding: 40px 0
  line-height: 1

  @include max-width($screen-md)
    padding: 20px 0

  & + &
    border-left: 1px solid $color-line-light

  &__big
    font-weight: 600
    font-size: 2rem

    @include min-width($screen-lg)
      font-size: 3rem

  &__sub
    font-size: 1.3rem

