.list
  margin: 60px 0
  display: flex
  flex-wrap: wrap
  border-top: 1px solid $color-line-dark

  &:first-child
    margin-top: 0

.list-item
  position: relative
  width: 100%

  &::before
    content: ''
    @include abs-cover
    z-index: -1
    background: rgba($color-accent, .05)
    opacity: 0
    transition: opacity 300ms

  @include min-width($screen-md)
    width: 50%

  &--full
    width: 100%

  &:hover
    &::before
      opacity: 1

    .list-item__name
      transform: translate3d(10px, 0, 0)

    .list-item__icon
      opacity: 1


  &__inner
    display: flex
    align-items: center
    padding: 30px
    border-bottom: 1px solid $color-line-dark
    font-size: 1.4rem
    font-weight: 600


    &::before
      content: '●'
      font-size: .7em
      padding-right: inherit
      color: $color-accent
      opacity: 0.2

    @include min-width($screen-md)
      &:nth-child(odd)
        border-right: 1px solid $color-line-dark

  &__name
    position: relative
    transition: transform 300ms

  &__icon
    margin-left: auto
    color: $color-accent
    opacity: 0.3
    transition: opacity 300ms

    svg
      width: 1em
      height: 1em

.banner
  position: relative
  z-index: 15
  background: url($noise-url) $color-accent
  color: $white

  @include min-width($screen-lg)
    @include main-container

  &__inner
    position: relative
    z-index: 1
    padding: 80px

    @include max-width($screen-lg)
      width: 90%
      margin: 0 auto
      padding: 40px 0

  &__subtitle
    font-size: 1.8rem
    line-height: initial
    margin-bottom: 1.25em

    @include max-width($screen-lg)
      font-size: 1.6rem

  &__title
    font-size: 3.2rem
    line-height: 1.2
    margin-bottom: 2em
    max-width: 730px

    @include max-width($screen-lg)
      font-size: 2.4rem

  &__btns
    @include max-width($screen-lg)
      display: flex
      > *
        flex: 1
    * + *
      margin-left: 1.5em

  &__img
    position: absolute
    top: 0
    right: 0
    bottom: 0
    width: 50%

    @include max-width($screen-lg)
      display: none

.carousel
  margin: 80px 0

.advs
  display: flex
  flex-wrap: wrap

.adv
  width: 100%
  padding: 20px
  color: $white
  border-top: 1px solid $color-line-light
  border-bottom: 1px solid $color-line-light
  border-right: 1px solid $color-line-light

  @include min-width(750)
    width: 50%
    padding: 40px

  @include min-width(1300)
    width: 33.33%

  // &__subtitle
  //   margin-top: 40px
  //

  &__icon
    @include flex-center
    width: 52px
    height: 52px
    background: $white
    border-radius: 50%

  &__title
    font-weight: 500
    font-size: 2rem
    margin: 1em 0

  &__description
    font-size: 1.4rem

.reviews
  margin: 40px 0

.review
  display: block
  padding: 40px
  border-bottom: 1px solid $color-line-dark

  @include max-width($screen-md)
    padding: 20px 0

  &:hover
    background: rgba($color-accent, .05)

  &__inner
    max-width: 760px


  &__header
    display: flex
    justify-content: space-between
    align-items: center
    margin-bottom: 20px

  &__name
    font-weight: 600
    font-size: 1.6rem

.footer
  position: relative
  z-index: 10
  background: url($noise-url), darken(#0071FE, 0%)
  color: $white
  @include max-width($screen-md)
    text-align: center
    padding-top: 40px

  &__inner
    position: relative
    display: flex
    flex-wrap: wrap

    // @include max-width($screen-lg)
    //   &::before
    //     content: ""
    //     @include decor-line
    //     right: 0

  &__col
    position: relative
    width: 25%

    @include max-width($screen-lg)
      width: 50%

    @include max-width($screen-md)
      width: 100%

    &:nth-child(3)
      @include min-width($screen-md)
        &::before
          content: ""
          @include decor-line
          left: 0

    // &:last-child
    //   @include min-width($screen-lg)
    //     &::after
    //       content: ""
    //       @include decor-line
    //       right: 0
    //
    &--p
      padding: 60px 0

      @include max-width($screen-md)
        padding: 20px 0

  &__logo
    margin-bottom: 40px
    svg
      width: 120px

    @include max-width($screen-md)
      margin-bottom: 20px

  &__cr
    margin-top: 20px
    font-size: 1.2rem
    opacity: .5

  &__nav
    &__item
      font-size: 1.6rem
      position: relative

      & + &
        margin-top: .5em

    &__title
      font-size: 1.6rem
      margin: 0 0 30px 0

  &__btn
    position: relative
    display: flex
    justify-content: center
    align-items: center
    width: 100%
    height: 50%
    padding: 60px 0
    border-bottom: 1px solid $color-line-light
    border-right: 1px solid $color-line-light

    @include min-width($screen-md)
      &:nth-child(1)
        border-top: 1px solid $color-line-light

    @include max-width($screen-md)
      height: auto
      border: none

      &:nth-child(2)
        border-bottom: none

    @include max-width($screen-lg)
      padding: 40px 0

    &::before
      content: ''
      position: absolute
      top: 0
      left: 0
      right: 0
      bottom: 0
      pointer-events: none
      background: $white
      opacity: 0
      transition: opacity 300ms

    &:hover
      &::before
        opacity: 0.05


    &__inner
      display: inline-block

    &__name,
    &__text
      display: block

    &__name
      opacity: .75

    &__text
      font-weight: 500
      font-size: 1.6rem

    &__icons
      display: flex


    &__icon
      display: inline-block
      width: 24px
      height: 24px
      margin-top: 4px

      & + &
        margin-left: 1em

.cases
  position: relative
  min-width: 100%
  width: 0
  overflow: initial !important
  margin: 60px 0
  z-index: 20 !important

  &:first-child
    margin-top: 0

.case
  width: auto !important

  &__thumb
    height: 440px
    background: red

  &__img
    height: 100%

  &__description
    display: inline-block
    margin-top: 20px
    line-height: 1.2

  &__name
    font-size: 1.6rem
    font-weight: 500
    margin-bottom: .3em

  &__info
    font-size: 1.4rem
    opacity: .8


// Call request
.is-call-request-opened
  .req
    pointer-events: auto
    left: 0
    transition-delay: 0ms
    visibility: visible

    &__bg
      opacity: .5

    &__main
      transform: translate3d(0, 0, 0)

.req
  position: fixed
  top: 0
  right: 0
  bottom: 0
  left: -100%
  z-index: 100
  pointer-events: none
  overflow: hidden
  transition-delay: 300ms
  visibility: hidden

  &__bg
    @include abs-cover
    background: #000
    opacity: 0
    cursor: pointer
    transition: opacity 300ms

  &__main
    position: absolute
    top: 0
    right: 0
    bottom: 0
    display: flex
    flex-direction: column
    // justify-content: space-between
    width: 100%
    max-width: 460px
    background: $white
    padding: 60px
    overflow-y: auto
    transform: translate3d(100%, 0, 0)
    transition: transform 300ms

  &__body
    margin: 60px 0

  &__title
    font-size: 2.6rem
    font-weight: 500
    margin: 0 0 .7em 0

  &__subtitle
    font-size: 1.3rem

  &__footer
    margin-top: auto

  &__cr
    text-align: center
    font-size: 1.1rem

  &__close
    position: absolute
    top: 40px
    right: 40px
    width: 24px
    height: 24px
    z-index: 10
    cursor: pointer

.field
  & + &
    margin-top: 40px

  &__label
    position: relative
    display: inline-block
    font-size: 1.2rem

    &--required
      &::after
        content: '*'
        font-size: 1.2rem
        color: $color-accent
        padding-left: 0.2em

  &__input
    display: block
    width: 100%
    padding: .7em 0

    &:focus,
    &:active,
    &:not(:empty)
      & + .field__decor
        &::before
          opacity: 1

  &__decor
    position: relative
    display: block
    height: 1px
    background: $color-line-dark

    &::before
      content: ''
      position: absolute
      right: 0
      bottom: 0
      left: 0
      height: inherit
      background: $color-accent
      opacity: 0
      transition: opacity 300ms

// Mobile menu
.is-mobile-menu-opened
  .mobile-menu
    left: 0
    transition-delay: 0ms
    pointer-events: auto

    &__bg
      opacity: .5

    &__main
      transform: translate3d(0, 0, 0)

.mobile-menu
  position: fixed
  top: 0
  right: 0
  bottom: 0
  left: -100%
  z-index: 100
  transition-delay: 300ms
  pointer-events: none

  @include min-width($screen-lg)
    display: none

  &__bg
    @include abs-cover
    // background: darken($color-accent, 45%)
    background: #000
    opacity: 0
    transition: opacity 300ms

  &__logo
    // @include flex-center
    display: flex
    svg
      width: 100px

  &__main
    position: absolute
    top: 0
    left: 0
    bottom: 0
    display: flex
    flex-direction: column
    width: 100%
    overflow-y: auto
    max-width: 320px
    background: $white
    transform: translate3d(-100%, 0, 0)
    transition: transform 300ms
    // padding: 60px 0

  &__header
    padding: 40px
    border-bottom: 1px solid $color-line-dark

  &__body
    margin-bottom: 40px

  &__item

    &--line
      border-bottom: 1px solid $color-line-dark

  &__link
    font-size: 1.6rem
    font-weight: 500
    display: block
    padding: 1.4em 40px

    &--small
      font-size: 1.4rem
      padding: 20px 0

    &:hover
      background: rgba($color-accent, .05)

  &__footer
    margin-top: auto
    text-align: center
    background: rgba($color-accent, .05)

  &__social
    display: flex
    justify-content: center
    padding: 20px 0

    li + li
      margin-left: 20px


  &__icon
    svg
      width: 24px
      height: 24px
